export default Page;

export const query = graphql`
  query {
    recruits: allFile(
      filter: { relativePath: { regex: "/^recruit-/" } }
      sort: { relativePath: ASC }
    ) {
      edges {
        node {
          childImageSharp {
            gatsbyImageData(width: 1920, placeholder: BLURRED)
          }
        }
      }
    }
    voices: allFile(
      filter: { relativePath: { regex: "/^voice-/" } }
      sort: { relativePath: ASC }
    ) {
      edges {
        node {
          childImageSharp {
            gatsbyImageData(width: 240, height: 240, placeholder: BLURRED)
          }
        }
      }
    }
  }
`;

export const Head = () => <Seo title='採用情報' />;
